<template>
  <div id="map" class="map-view">
    <!-- <span class="loading-screen" v-show="isLoading">

    </span> -->
<transition name="fade">
      <div class="popup-dialog" style="" v-show="speed_visible" :style="{

     'left': speed_left + 'px',
     'top': speed_top + 'px'
   }"> <div class="speed">{{speed_text}}</div> </div>
</transition>


    <transition name="fade">
      <div class="popup-dialog" style="" v-show="pop_visible" :style="{

     'left': pop_left + 'px',
     'top': pop_top + 'px'
   }">

        <div class="light" v-if=" pop_isGreen ? pop_gtime1 : pop_time > 0" :style="{
       
        'background-color': pop_isGreen ? '#67c23a' : '#f56c6c'
        }"> {{ pop_isGreen ? pop_gtime1 : pop_time1 }} s</div>

        <div class="light" :style="{
      
          'background-color': pop_isGreen ?  '#f56c6c' : '#67c23a',
        }"> {{ pop_isGreen ? pop_gtime2 : pop_time2 }} s</div>

        <div class="light" :style="{
        'background-color': pop_isGreen ? '#67c23a' : '#f56c6c'
        }"> {{ pop_isGreen ? pop_gtime3 : pop_time3 }} s </div>

        <div class="light" v-if=" pop_isGreen ? pop_gtime4 : pop_gtime4 > 0" :style="{
        'background-color': pop_isGreen ? '#f56c6c': '#67c23a'
        }"> {{ pop_isGreen ? pop_gtime4 : pop_gtime4 }} s </div>

      </div>
    </transition>

    <div id="simulation" v-show="isSimulation_open">
      <div class="title">
        <h2>{{isSimulation_text}}</h2>
        <el-button @click="simulation_close" size="mini" type="info" icon="el-icon-close" plain circle></el-button>
      </div>



      <div class="content">
         <div class="loader" v-if="isSumulation_loading"></div>
        <el-button @click="simulation_download" type="danger"  :disabled="isSumulation_loading" plain> Download Simulation</el-button>
        <el-button @click="isSumulation_loading = false"  v-if="!isSumulation_downloading && isSumulation_loading" type="warning"   plain><i class="fas fa-stopwatch"></i> Cancel</el-button>
        
            <div class="block" >
               <el-button  v-if="isSimulation_play" type="primary"  @click="simulation_pause" :disabled="!isSumulation_finish" round><i class="fas fa-pause"></i></el-button>
            
              <el-button v-else type="success" icon="el-icon-caret-right" @click="simulation_play" :disabled="!isSumulation_finish" round></el-button>
               <el-slider @change="simulation_change" v-model="simulation_time" :disabled="!isSumulation_finish" :min="0" :max="simulation.length" show-input></el-slider>
            </div>
      </div>

      
      <!-- <div class="loader" v-if="!isSimulation_finish"></div>
      <strong class="score-header">
        <strong class="score">{{isLoading_score}}</strong>
      </strong> -->

    </div>

    <!-- <el-dialog :visible="isSimulation_open" id="loadingdialog" 
      @close="simulation_close"
      :show-close="isSimulation_finish" 
      :close-on-click-modal="isSimulation_finish"
      :close-on-press-escape="isSimulation_finish"

      class="loading-dialog" 
      :title="isSimulation_text" width="60%"  style="z-index: 100001; margin-top: 0; ">
        <div class="loader" v-if="!isSimulation_finish"></div>
        <strong class="score-header">
        <strong class="score">{{isLoading_score}}</strong> </strong>
      </el-dialog> -->

    <el-dialog :visible="isLoading_open" id="loadingdialog" @open="loading_open" @close="loading_close" :show-close="isLoading_finish" :close-on-click-modal="isLoading_finish" :close-on-press-escape="isLoading_finish" class="loading-dialog" :title="isLoading_text" width="60%" style="z-index: 100001; margin-top: 0; ">
      <div class="loader" v-if="!isLoading_finish"></div>
      <strong class="score-header">
        <strong class="score">{{isLoading_score}}</strong>
      </strong>
      <canvas id="myChart" width="600" height="170"></canvas>
    </el-dialog>

    <el-dialog id="traffic_info" class="traffic_dialog" title="Info" @close="traffic_visible = ''" :visible="traffic_visible == 'info' " width="450px" center>
      <span>

        <h2>Goal</h2>
        <hr/>
        <p>The goal of the project is to visualize our traffic signal control algorithms.</p>
        <br/>
        <h2>Representation</h2><hr/>

        <p>The
          <strong>map</strong> presents traffic signal settings for 21 crossroads at
          <strong>Stara Ochota district in Warsaw</strong>.
          <br/><br/> Each number next to a traffic signal is an offset - number from the set {0,1,2,...,119} representing the number of seconds from the beginning of the simulation to the first switch from a
          <strong>red</strong> signal phase to a
          <strong>green</strong> signal phase for a given signal. For each crossroad, we visualize offsets only for 1 traffic signal being a representative of the whole crossroad (the rest is synchronized to ensure traffic safety), to see the complete configuration of red and green signal states, one should click on a given traffic signal - then, it is possible to modify offsets.</p>
        <br/>
        <h2>Current Settings</h2><hr/>

        <p>In the current setting, we assume that durations of red signal phases are always 62 seconds, while durations of green signal phases are always 58 seconds. </p><br/>

        <p>For each setting, we compute
          <strong>"Traffic score"</strong>: the total time of waiting on red signals during
          <strong>10 minutes of traffic</strong> (we can simulate it, but we can also use machine learning algorithms, trained on data from simulations to speed up evaluations). </p>

        <p>We can also run an optimization algorithm (based on a genetic algorithm) to find a good setting (we can run it using the
          <strong>"Find best setting"</strong> button).</p>

      </span>
    </el-dialog>

    <el-dialog id="traffic_dialog_id" class="traffic_dialog" title="Update an offset for a traffic signal" :visible="traffic_visible == 'traffic'" width="40%" @close="traffic_dialog_off" center>

      <span>The signal starts from time 0 and its state is calculated by the offset of the value: </span>

      <el-slider class="mt-2" show-input placeholder="Please input" v-model="traffic_time" :min="0" :max="120" />

      <div v-for="s in traffic_signs" :key="s.id">
        <span class="quote">Traffic signal at position: {{s.position}} </span>
        <div class="line">
          <img class="first" src="signal.png" alt="">
          <div class="color123 " :style="{
            'width': (s.isGreen ? traffic_gtime1 : traffic_time1)/60*100 + '%',
            'background-color': s.isGreen ? '#67c23a' : '#f56c6c'
            }"> {{(s.isGreen ? traffic_gtime1 : traffic_time1)}}</div>

          <div class="color123" :style="{
            'width': 
           (s.isGreen ? traffic_gtime2 : traffic_time2)/60*100 + '%',
              'background-color': s.isGreen ?  '#f56c6c' : '#67c23a',
            }"> {{(s.isGreen ? traffic_gtime2 : traffic_time2)}}</div>

          <div class="color123" :style="{
            'width': (s.isGreen ? traffic_gtime3 : traffic_time3)/60*100 + '%',
            'background-color': s.isGreen ? '#67c23a' : '#f56c6c'
            }"> {{(s.isGreen ? traffic_gtime3 : traffic_time3)}} </div>

          <div class="color123 " :style="{
                'width': (s.isGreen ? traffic_gtime4 : traffic_time4)/60*100 + '%',
                'background-color': s.isGreen ?  '#f56c6c' : '#67c23a'
                }"> {{(s.isGreen ? traffic_gtime4 : traffic_time4)}} </div>

          <div>

          </div>

        </div>
        <div></div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="traffic_dialog_off">Cancel</el-button>
        <el-button type="primary" @click="traffic_dialog_confirm">Update Offset</el-button>
      </span>
    </el-dialog>

    <MessageBox title="Parameters of a genetic algorithm" ok="Run Genetic Algorithm" v-if="isShowGenetic" @cancel="isShowGenetic=false" @run="genetic_calculate()">

      <el-form ref="form" :model="genetic_config" label-width="170px">
        <el-form-item label="Number of iterations">
          <el-input v-model="genetic_config.iterations"></el-input>
        </el-form-item>
        <el-form-item label="Population size">
          <el-input v-model="genetic_config.size"></el-input>
        </el-form-item>
        <el-form-item label="Probability of crossover">
          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="When two individuals are selected for mating.">
            <el-input slot="reference" v-model="genetic_config.crossover"></el-input>
          </el-popover>
        </el-form-item>
        <el-form-item label="Probability of mutation">

          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="Probability of change single single chromosome during mutation to prevent local minima.">
            <el-input slot="reference" v-model="genetic_config.mutation"></el-input>
          </el-popover>
        </el-form-item>
      </el-form>

    </MessageBox>

    <div id="mapid">
    </div>

    <div class="panel-info">

      <el-popover class="traffic-score" placement="top-start" title="" width="200" trigger="hover" content="The total time of waiting on  a red signal for the current signal setting configuration">

        <el-button class="traffic-score" slot="reference" type="primary" plain @click="showCalculation">
          <i class="fas fa-trophy"></i> Traffic score: {{Math.round(time,2)}} s</el-button>
      </el-popover>
      <!-- <el-button type="primary" plain @click="showCalculation">
        <i class="fas fa-traffic-light"></i> Traffic Lights</el-button> -->
      <el-button type="danger" plain @click="simulation_open">
        <i class="fas fa-car">
        </i> Simulation <span v-if="simulation_score!=''">{{simulation_score}} s</span></el-button>
      <el-button type="success" plain @click="traffic_visible = 'info'">
        <i class="el-icon-question"></i> Info</el-button>
    </div>

    <div class="panel-show" v-show="panelType=='cal'">
      <!-- <span>
        Result:
        <span class="text-result">{{Math.round(time)}} s</span>
      </span> -->
      <!-- <hr> -->
      <el-button class="calculation" type="warning" plain @click="isShowGenetic = true">
        <i class="fas fa-calculator"></i> Find best setting</el-button>

      <span/>

      <el-popover placement="top-start" title="" width="200" trigger="hover" content="Load best solution found so far">

        <el-button slot="reference" class="calculation" type="success" plain @click="traffic_load(best_solution)">
          <i class="fas fa-upload"></i> Load Best Solution ({{Math.round(best_solution_result)}} s)</el-button>
      </el-popover>

      <div class="light" v-for="group in sign_groups" :key="group.id" @click="traffic_dialog_update(group.sign_marker)" @mouseover="calOnOver(group)" @mouseout="calOffOver(group)">
        <!--  -->

        <span class="text" @click.self="traffic_dialog_update(group.sign_marker)">
          {{group.id}}:
        </span>
        <span class="time">{{group.time}}</span>
        <!-- <el-input :value="group.time" @input="traffic_set_group(group,$event,true)" size="mini" class="time" type="number " /> -->
        <span class="inside-icon">
          <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
        </span>

      </div>

    </div>

    <div class="panel-show" v-show="panelType=='sim'">
      <div></div>
      <el-button class="calculation" type="primary" plain @click="simulation_load">Load Simulation</el-button>
      <el-slider v-model="simulation_time" :min="0" :max="600"></el-slider>
      <el-button type="success" round icon="el-icon-caret-right" @click="simulation_play">Start Simulation</el-button>
    </div>

  </div>
</template>
