function gtime1(time) { //green
      return  (time == 0) ? 0
      : time == 1 ? 0
      : time == 120 ? 0
      : time<=60 ? time-2
      : 0 // time-62;
    }

function gtime2(time) { //red
    return  (time == 0 )  ? 60
      :  time == 1 ? 61
      : time == 120 ? 60
      :  time<=60 ? 62
      :  time % 60
  }

  function gtime3(time) { //green
    return (time == 0 ) ? 58
      :  time == 1 ? 58
      : time == 120 ? 58
      : time<=60 ? 60-time
      : 58;
    }

  function gtime4(time) { //red
    return (time == 0 ) ? 2
      : time == 1 ? 1
      : time == 120 ? 2
      : time<=60 ? 0
      : 122-time;
    }



   function rtime1(time) { //red
      return  (time == 0 )  ? 0
      : time==120 ? 0
      : time == 1 ? 1
      : time<=62 ? time
      : 0;
    }

function rtime2(time) { //green
    return  (time == 0 )  ? 58
       : time==120 ? 58
      : time == 1 ? 58
      : time<=62 ? 58
      : time - 62
  }

  function rtime3(time) { //red
  return (time== 0 )  ? 62
     : time==120 ? 62
    :  time == 1 ? 61
    : time<=62 ? 62-time
    : 62;
  }

   function rtime4(time) { //green
   return (time== 0 )  ? 0
     : time==120 ? 0
    :  time == 1 ? 0
    : time<=62 ? 0
    : 120-time;
    }

const delay = ms => {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

const send_http = function(url,body)  {
  return new Promise(resolve => {
     var xmlhttp = new XMLHttpRequest();   // new HttpRequest instance 
     xmlhttp.timeout = 100000;
        xmlhttp.onreadystatechange = function() {
            if (this.readyState == 4 && (this.status == 200 || this.status == 201)) {
                resolve(this.response);
                // var status = JSON.parse(this.response);
                // v.simulation_score = status.score;
                // v.simulation_download_times();

            }
        };  
        xmlhttp.open("POST", url);
        xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlhttp.send(body);
  })
}
     
// process.env.NODE_TLS_REJECT_UNAUTHORIZED = "0";
// #console.log(process.env.NODE_TLS_REJECT_UNAUTHORIZED);
